import { WindowProxy } from "./window";
export default class MessageProxy {
    constructor(scope) {
        return new Proxy(scope.addEventListener, {
            apply(target, thisArg, args) {
                if (args[0] && args[1]) {
                    if (args[0] === "message") {
                        args[1] = new Proxy(args[1], {
                            apply(target, thisArg, args) {
                                args[0] = new Proxy(args[0], {
                                    get(target, property, receiver) {
                                        if (property === "origin")
                                            return __location.origin;
                                        if (property === "path")
                                            target[property].map((win) => new WindowProxy(win));
                                        if (property === "currentTarget")
                                            return new WindowProxy(target[property]);
                                        if (property === "source")
                                            return new WindowProxy(target[property]);
                                        if (property === "srcElement")
                                            return new WindowProxy(target[property]);
                                        if (property === "target")
                                            return new WindowProxy(target[property]);
                                        return target[property];
                                    }
                                });
                                return Reflect.apply(target, thisArg, args);
                            }
                        });
                    }
                }
                return Reflect.apply(target, thisArg, args);
            }
        });
    }
}
